.item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 1em;
  padding: 0.5em 0;

  border: 1px solid var(--main-color);
}

.icon {
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 50%;
  margin: 1em;
}

.body {
  margin-right: 1em;
}
